@font-face {
  font-display: swap;
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
  src: url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-display: swap;
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2");
  src: url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-display: swap;
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Italic.woff2") format("woff2");
  src: url("../fonts/Montserrat-Italic.woff") format("woff");
  font-weight: 400;
  font-style: italic; }

@font-face {
  font-display: swap;
  font-family: 'Montserrat';
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2");
  src: url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal; }

html {
  box-sizing: border-box; }

* {
  box-sizing: inherit;
  list-style-type: none; }

html, body {
  position: relative;
  height: 100%; }

body {
  width: 100%;
  margin: 0 auto;
  padding: 0;
  font-family: Montserrat, sans-serif;
  background-color: black;
  background-image: url(../img/Presentation.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top 40px left 0px; }

a {
  text-decoration: none;
  list-style-type: none; }

a:visited {
  background-color: white; }

img {
  height: auto; }

button:hover {
  outline: 0;
  background-color: grey; }

.header {
  margin: 0 auto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px; }

.main-nav {
  display: flex;
  margin: 0 auto;
  justify-content: center; }
  .main-nav__list {
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0; }
  .main-nav__item {
    margin-right: 15px;
    margin-top: 62px; }
  .main-nav__link {
    list-style: none;
    color: white;
    cursor: pointer;
    font-size: 15px;
    text-transform: uppercase; }

.about {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin: 0 auto; }
  .about__content {
    width: 100%;
    color: white;
    margin: 0;
    padding: 0;
    text-align: center; }
  .about__headline {
    font-size: 100px;
    font-style: normal;
    font-weight: bold;
    font-size: 150px; }
  .about__signature {
    height: 171px;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    font-size: 22px; }
  .about__content-text {
    display: none;
    font-style: normal;
    font-weight: 300;
    font-size: 24px; }
    .about__content-text--link {
      text-decoration-line: underline;
      color: white; }
  .about__content-inner {
    width: 90%;
    margin: 0 auto; }

.swiper-container {
  width: 100%;
  height: 100%; }

.swiper-slide {
  text-align: center;
  font-size: 18px; }

.contacts__content {
  height: 200px;
  background-color: #fff;
  color: #4F4F4F; }

.ymaps-layers-pane {
  filter: grayscale(100%);
  filter: url(/img/filters.svg#grayscale);
  /* Firefox 3.5+ */
  -webkit-filter: grayscale(1);
  /* Google Chrome & Safari 6+ */ }

.projects {
  display: flex;
  flex-direction: column; }
  .projects__preview {
    padding: 50px 0; }
  .projects__demo {
    display: flex;
    width: 80%;
    margin: 0 auto;
    flex-direction: column;
    background-color: white; }
  .projects__demo-name {
    margin-left: 5%;
    font-size: 50px;
    color: #000; }
  .projects__demo-video {
    display: flex;
    justify-content: center; }
  .projects__demo-iframe {
    width: 90%;
    height: 675px; }
  .projects__demo-signature {
    margin-left: 5%;
    padding-top: 10px;
    color: #000;
    font-size: 25px; }
  .projects__demo-gallery {
    display: flex;
    width: 100%;
    margin: 0;
    padding: 0; }
  .projects__demo-impressions {
    display: flex;
    margin-left: 2.5%;
    min-height: 700px;
    flex-direction: row;
    flex-wrap: nowrap; }
    .projects__demo-impressions--text {
      width: 50%; }
    .projects__demo-impressions--foto {
      width: 57%;
      height: auto;
      background-image: url(../img/project-1.png);
      background-repeat: no-repeat;
      background-position: bottom 0px right 0px; }

.music {
  display: flex;
  width: 100%;
  flex-direction: column;
  margin-top: 50px;
  margin-bottom: 50px;
  color: white; }
  .music__inner {
    width: 80%;
    margin: 0 auto;
    flex-direction: row; }
  .music__name {
    display: flex;
    width: 100%;
    padding-top: 136px;
    padding-bottom: 64px;
    font-style: normal;
    font-weight: 300;
    font-size: 42px; }
  .music__block {
    display: flex;
    margin: 0 auto;
    flex-direction: column; }
  .music__track {
    display: flex;
    width: 70%; }
  .music__player {
    width: 95%;
    margin: auto;
    padding-bottom: 15px;
    text-align: center;
    border-bottom: solid 1px grey; }
    .music__player--button {
      width: 25px;
      height: 25px;
      margin-top: 25px;
      background-image: url(../img/play-button-white.png);
      background-size: cover;
      border: none;
      cursor: pointer; }
    .music__player--button:hover {
      background-color: white; }
    .music__player--title {
      padding: 10px; }
    .music__player--controls {
      display: flex;
      margin-top: 16px;
      padding: 0;
      flex-direction: row;
      flex-wrap: nowrap;
      align-items: center;
      justify-content: space-between; }
    .music__player--duration {
      margin-left: 10px; }

.progressbar {
  width: 80%; }

.feedback {
  display: flex;
  position: relative;
  width: 100%;
  flex-direction: column;
  margin-top: 50px;
  margin-bottom: 50px;
  color: white; }
  .feedback__form {
    display: flex;
    width: 90%;
    flex-direction: column;
    margin: auto; }
  .feedback__form-headline {
    width: 100%; }
  .feedback__form-letter {
    display: flex;
    width: 100%;
    flex-direction: row;
    flex-wrap: nowrap; }
    .feedback__form-letter--text {
      width: 60%;
      min-height: 100%; }
    .feedback__form-letter--textarea {
      width: 100%;
      height: 100%;
      background-color: black;
      border: none;
      color: #fff; }
    .feedback__form-letter--personal {
      display: flex;
      width: 45%; }
    .feedback__form-letter--confirm {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: space-between; }
  .feedback__modal {
    display: none; }

.modal--open {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100; }

.modal__dialog {
  display: flex;
  margin: 0 auto; }

.modal__content {
  display: flex;
  position: relative;
  color: black;
  font-size: 50px;
  padding: 10px;
  background-color: #fff; }
  .modal__content--btn {
    position: absolute;
    top: 10px;
    right: 0;
    font-size: 20px;
    background-color: #fff;
    border: none;
    cursor: pointer; }

.overlay {
  background: #000;
  opacity: 0.8;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 90;
  display: none; }
